import ImageWithText from '../../components/image'
import ImageContainer from '../../components/image_container'
import SliderComponent from '../../components/cards_container';
import SliderComponentvid from '../../components/cards_video';
import NavbarMui from '../../components/NavRsp/NavMui'
import SliderComponentmovil from '../../components/cardvidmovil';
import SliderComponentmv from '../../components/cards_containermv';
import '../../App.css';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.js";
import FormWithText from '../../components/form_text'
import Footer from '../../components/footer'
import Textvidmob from '../../components/textvidmob';
import './landing.css'
import React, { useState } from 'react';
import { useEffect } from 'react';
import MisionComponent from '../../components/gridtxt';
import Textvidinclud from 'components/textinclud';
import  TextimgH  from '../../components/howdoit2';
import TextimgH2 from '../../components/howdoit';
import TextimgH3 from '../../components/howdoit3';
import TextimgH4 from '../../components/howdoit4';
import '@fortawesome/fontawesome-free/css/all.css';


const Landing = () => {
    const [isMobile, setIsMobile] = useState(false);
  
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };
  
    useEffect(() => {
      window.addEventListener("resize", handleResize);
      handleResize();
      return () => {
        window.removeEventListener("resize", handleResize);
      };
    }, []);
  
    return (
      <>
        <div className='landing-container'> 
          <NavbarMui />
          <ImageWithText />
          {isMobile ? (
            <>
             <Textvidmob />
             <Textvidinclud />
             <TextimgH />
             <TextimgH2 />
             <TextimgH3 />
             <TextimgH4 />
             </>
             ) : (
            <div style={{ margin: '0 6rem' }}>
            <Textvidmob />
            <Textvidinclud />
            <TextimgH />
            <TextimgH2 />
            <TextimgH3 />
            <TextimgH4 />
            </div>)}
           

          <MisionComponent />
          <div>
            <ImageContainer />
          </div>
  
          {isMobile ? (
            <div className="slider-container">
              <h2 style={{ fontSize: "28px", color: "#84007B", textAlign: "left", marginBottom: "20px", marginLeft: "50px" }}>Experiencias en Colegios</h2>
              <div className="slider-container-comp1" style={{ marginLeft: "60px", marginRight: "30px" }}>
                <SliderComponentmovil />
              </div>
            </div>
          ) : (
            <div className="slider-container">
              <h2 style={{ fontSize: "28px", color: "#84007B", textAlign: "left", marginBottom: "20px", marginLeft: "50px" }}>Experiencias en Colegios</h2>
              <div className="slider-container-comp1" style={{ marginLeft: "60px", marginRight: "30px" }}>
                <SliderComponentvid />
              </div>
            </div>
          )}
  
          <div>
            {isMobile ? (
              <div className="slider-container" style={{ marginBottom: "20px", height: "77%", backgroundColor: '#FCD107' }}>
                <h2 style={{ fontSize: "35px", color: "black", textAlign: "center", marginBottom: "40px", marginLeft: "50px" }}>Nuestros Aliados</h2>
                <div className="slider-container" style={{ marginLeft: "50px", marginRight: "10px" }}>
                  <SliderComponentmv />
                </div>
              </div>
            ) : (
              <div className="slider-container" style={{ marginBottom: "20px", height: "77%", backgroundColor: '#FCD107' }}>
                <h2 style={{ fontSize: "35px", color: "black", textAlign: "center", marginBottom: "40px", marginLeft: "50px" }}>Nuestros Aliados</h2>
                <div className="slider-container" style={{ marginLeft: "80px", marginRight: "10px" }}>
                  <SliderComponent />
                </div>
              </div>
            )}
          </div>
  
          <FormWithText />
          <Footer />
        </div>
      </>
    );
  };
  
  export default Landing;